import React, { useState } from 'react';
import {
    Switch,
    Route,
    Redirect,
  } from "react-router-dom";
import Home from '../components/home/home';
import Restaurents from '../components/restaurents/restaurents';
import Faq from '../components/faq/faq';
import GiftCard from '../components/giftcards/giftcard';
import GiftCard2 from '../components/giftcards/giftcard2';
import GiftCard3 from '../components/giftcards/giftcard3';
import GiftCard4 from '../components/giftcards/giftcard4';
import DriveWithUs from '../components/drivewithus/drivewithus';
import PartnerWithUs from '../components/partnerwithus/partnerwithus';
import Login from '../components/auth/login';
import Register from '../components/auth/register';
  function Routes() {
    return (
        <Switch>
            <Route exact path = "/">
                <Home />
            </Route>
            <Route exact path="/restaurents">
                  <Restaurents />
            </Route>
            <Route exact path="/faq">
                  <Faq />
            </Route>
            <Route exact path="/gift-card-1">
                  <GiftCard />
            </Route>
            <Route exact path="/gift-card-2">
                  <GiftCard2 />
            </Route>
            <Route exact path="/gift-card-3">
                  <GiftCard3 />
            </Route>
            <Route exact path="/gift-card-4">
                  <GiftCard4 />
            </Route>
            <Route exact path="/dwithus">
                  <DriveWithUs />
            </Route>
            <Route exact path="/pwithus">
                  <PartnerWithUs />
            </Route>
            <Route exact path="/login">
                  <Login />
            </Route>
            <Route exact path="/register">
                  <Register />
            </Route>
        </Switch>
    )   
  }

  export default Routes;